.single .content p {
    margin: 1.5rem 0;
}

#content.content {
	line-height: 1.9em;
}
[theme=light] #content.content {
    color: #222222;
}

.single .content h2, .single .content h3, .single .content h4, .single .content h5, .single .content h6 {
    margin: 2.2rem 0 2rem 0;
}

.single .content blockquote{
	border-left: 0.5rem solid #d9d9d9;
    background-color: #f5f5f5d4;	
}

.single .content p {
    margin: 1.5rem 1rem;
}

#toc-static .toc-title {
    background: #e6e6e65c;
}

