.tag-cloud-tags {
	font-family: "Biaodian Pro Serif GB","Numeral LF Serif","Minion Pro","Palatino Linotype", "Book Antiqua", Palatino,Georgia,"Times New Roman",STKaiti,"STKaiti SC","Huawen Kaiti","Huawenkaiti","Han Kaiti GB",Kaiti,"Han Kaiti",Kai,cursive,serif;
	color: #f5f5f5d4;
}

.single .post-footer .post-info {
    padding: 5rem 0 0.3rem;
}

.single .post-footer .post-info-more {
    padding: 0.3rem 0 3rem;
}

.single-title {
	padding: 2rem 0 3rem 0;
}

.archive-item-date {
    text-transform: uppercase;
}

.archive .archive-item {
	margin: 1rem 0 0.75rem 1.5rem;
}


