header {
	background-color: #ffffffe3;
	-webkit-backdrop-filter: blur(3px);	
    backdrop-filter: blur(3px);
}

#header-desktop .header-wrapper .header-title {
    font-size: 1.3rem;
}

#header-mobile .header-container .header-wrapper .header-title {
    font-size: 1.3rem;
}

.search input {
    background-color: transparent;
}

.single .content hr {
    margin: 2rem 20%;
    position: relative;
    border-top: 3px solid #ebebeb;
    border-bottom: none;
}

[theme=dark] .single .content hr {
    margin: 2rem 20%;
    position: relative;
    border-top: 3px solid #5a5a5a;
    border-bottom: none;
}

[theme=black] .single .content hr {
    margin: 2rem 20%;
    position: relative;
    border-top: 3px solid #5a5a5a;
    border-bottom: none;
}

footer {
    height: 3rem;
}

.footer-line {
	color: #999;
}

.footer-line a {
	color: #999;
}

.footer-line a:hover {
	color: $global-link-hover-color;
}
[theme=dark] .footer-line a:hover {
	color: $global-link-hover-color-dark;
}
[theme=black] .footer-line a:hover {
	color: $global-link-hover-color-black;
}

g.cluster-label {
    line-height: 1.3em;
}

.page .mermaid {
    stroke: $global-font-color;
    line-height: 1.3em;
    font-family: "trebuchet ms", verdana, arial;
    height: auto;
}

#svg-id-1 {
    height: auto;
}

#svg-id-2 {
    height: auto;
}

#svg-id-3 {
    height: auto;
}

#svg-id-4 {
    height: auto;
}

#svg-id-5 {
    height: auto;
}

#svg-id-6 {
    height: auto;
}

#svg-id-7 {
    height: auto;
}

#svg-id-8 {
    height: auto;
}

#svg-id-9 {
    height: auto;
}

#svg-id-10 {
    height: auto;
}

[theme=dark] .page .mermaid {
    stroke: $global-font-color-dark;
}

[theme=black] .page .mermaid {
    stroke: $global-font-color-black;
}

.home-links {
    margin-top: 5rem;
    font-size: 0.875rem;
    word-break: keep-all;
    word-wrap: normal !important;
}


.home-blog {
    font-size: 0.875rem;
    font-family: "Noto Mono", "Space Mono", "Droid Sans Mono", "PT Mono", "Andale Mono", "Courier New", sans-serif;
    margin-bottom: 1.5rem;
}

.home-email {
    font-size: 0.875rem;
    font-family: "Noto Mono", "Space Mono", "Droid Sans Mono", "PT Mono", "Andale Mono", "Courier New", sans-serif;
    margin-bottom: 3rem;
}

.home-blog a:hover, .home-email a:hover {
    color: #00929a;
    background-color: #16bcc52b;
}

.home-arena {
    font-size: 0.875rem;
	color: rgb(153, 153, 153);
    margin-bottom: 1.5rem;
}

.home-arena-link {
    font-size: 0.875rem;
	color: rgb(75, 61, 103);
    font-weight: 600;
}

.home-weilan-link {
    font-size: 0.875rem;
    color: #424242;
}

.links a {
    color: #424242;
}

.home-arena-link-eng {
	word-break: keep-all;
	word-wrap: normal !important;
}

.home-arena a:hover {
	color: #16bcc5;
}

.home-insta a:hover {
    background: -webkit-linear-gradient(0deg, #fdf497 0%, #fdf497 5%, #fd5949 45% , #d6249f 60%, #285AEB 90%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 800;
}

p.social-link {
    word-break: keep-all;
    word-wrap: normal !important;
}

.mstd {
    margin-right: 0.5em;
    font-size: .875rem;
    color: #4ea2df;
    font-family: "mastodon-font-sans-serif",Roboto,sans-serif;
}

.rym {
    margin-left: 0.5em;
    font-size: .875rem;
    color: #3974C7;
    font-family: 'Proxima Nova',Arial,sans-serif;
}

.links a:hover {
    color: #16bcc5;
}



#toc-static {
	padding-bottom: 3rem;
}

