#content img {
	margin: auto;
	display: block;
}

textarea {
    padding: 1em;
}

.archive .single-title {
    text-align: center;
}

.archive .group-title {
    margin: 3rem 0;
}

.special .single-title, .special .single-subtitle {
    text-align: center;
}

.pagination {
    padding: 3rem 0;
}

.archive .archive-item-link {
    min-width: 10%;
	overflow: inherit;
    white-space: inherit;
    text-overflow: inherit;
}

::selection {
	background-color: #16bcc52b;
}

.btn {
	display: inline-block;
    vertical-align: baseline;
    min-width: 2.5em;
    margin-bottom: 0;
    padding: 0.5em 1em;
    border: 1px solid #999;
    border-radius: 0.5em;
    background: transparent;
    color: #999;
    font-weight: 400;
    font-size: .75em;
    line-height: 1.5;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    transition-duration: .4s;
    touch-action: manipulation;
}

.btn:hover {
	border-color: $global-link-hover-color;
	color: $global-link-hover-color;
}

[theme="black"] .single .content blockquote {
    border-left-color: #7c7c7cab;
    background-color: #a7a7a733;
}

[theme="dark"] .single .content blockquote {
    border-left-color: #7c7c7cab;
    background-color: #5252524d;
}

.page {
    padding-bottom: 2rem;
}

.cc-window {
    font-size: 0.875em;
}

.cc-window {
    background-color: #ffffffe3;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
}

[theme=light] div.cc-window {
    background-color: #ffffffe3;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
}

[theme=dark] div.cc-window {
    background-color: rgba(37,38,39,0.890196);
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    color: #fff;
}

[theme=black] div.cc-window {
    background-color: rgba(0, 0, 0, 0.89);
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    color: #fff;
}

a.cc-link {
    text-decoration: none!important;
    color: #fff!important;
}

div.music {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
}

a.home-sc-link {
    display: inline-block;
    margin-left: 0.3em;
    margin-top: 0.4em;
    color: #f50;
    font-family: Interstate,Lucida Grande,Lucida Sans Unicode,Lucida Sans,Garuda,Verdana,Tahoma,sans-serif;
    font-size: .825rem;
}

#playground {
    text-decoration: underline;
}

#playground:hover {
    text-decoration: line-through;
}

